import {httpHandle} from '@/http/http'

/**
 *  
*/


//  tree 
export const FactoryTreeHTTP= async(params={})=>{
    return await httpHandle({
        url:"/iiot/nodeLevel/list",
        method:'get',
        url_params:params
    })
}

