export const FaultTypeHTTP= async(params)=>{
    return await httpHandle({
        url:"/iiot/faultType/list?enabled=1",
        method:'get',
    })
}


//   
export const FaultPositionHTTP= async(params)=>{
    return await httpHandle({
        url:"/iiot/fault/list?enabled=1",
        method:'get',
    })
}


//   
export const MaintainTypeHTTP=async(params)=>{
    return await httpHandle({
        url:"/system/dict/data/type/equipment_repair_type",
        method:'get',
    })
}



//   
export const RepairmanHTTP=async(params)=>{
    return await httpHandle({
        url:"/iiot/equipmentRepair/userList?enabled=1",
        method:'post',
    })
}



//   
export const SubmitHTTP=async(params)=>{
    return await httpHandle({
        url:"/iiot/equipmentRepair/confirmRepairWbForApp",
        method:'post',
        payload:params.payload,

    })
}